
import { defineComponent, nextTick } from 'vue';
import grabImg from '../lib/grabImg';
import STATE from '../API/state';

export default defineComponent({
  setup() {
    const { state } = STATE;

    const gogo = () => {
      state.count += 1;
    };
    nextTick(() => {
      grabImg();
    });

    return {
      gogo,
    };
  },
});
